import React from "react";
import clsx from "clsx";
import { ButtonProps, Variant } from "./Button.types";
import "./Button.scss";

const ContestCard: React.FC<ButtonProps> = ({
  label,
  variant = Variant.PRIMARY,
  external = false,
  disabled = false,
  iconLeft,
  iconRight,
  href,
  onClick,
}) => {
  const styling = clsx({
    "button button--primary": variant === Variant.PRIMARY,
    "button button--secondary": variant === Variant.SECONDARY,
    "button button--primary button--primary--white":
      variant === Variant.WHITE_PRIMARY,
    "button button--secondary button--secondary--button--white":
      variant === Variant.WHITE_SECONDARY,
  });

  return href != null ? (
    <a
      target={external ? "_blank" : undefined}
      rel={external ? "noreferrer noopener" : undefined}
      href={href}
      className={styling}
    >
      {label}
    </a>
  ) : (
    <button
      type="button"
      className={styling}
      onClick={onClick}
      disabled={disabled}
    >
      {/* If passing a relative/absolute path as icon */}
      {iconLeft && typeof iconLeft === "string" && (
        <img src={iconLeft} alt="" />
      )}
      {/* If passing a React Node (svg, icon from icon library, etc..) as icon */}
      {iconLeft && typeof iconLeft !== "string" && iconLeft}
      {text}
      {/* If passing a relative/absolute path as icon */}
      {iconRight && typeof iconRight === "string" && (
        <img src={iconRight} alt="" />
      )}
      {/* If passing a React Node (svg, icon from icon library, etc..) as icon */}
      {iconRight && typeof iconRight !== "string" && iconRight}
    </button>
  );
};

export default ContestCard;
